@import url(https://cdn.jsdelivr.net/npm/@xz/fonts@1/serve/hk-grotesk.min.css);
* {
  font-family: -apple-system, BlinkMacSystemFont, 'HK Grotesk', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  height:100%;
}

body {
  margin: 0;
  padding-bottom: 70px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.checkout-wrapper {
    align-items: center;
    background-color: #fff;
    border-top: 1px solid #E8DFED;
    bottom: 0;
    box-sizing: border-box;
    display: flex;
    height: 70px;
    justify-content: space-between;
    padding: 0 20px;
    position: fixed;
    width: 100%;
}
.expense-wrapper {
    background-color: #250B38;
    display: flex;
    margin: 6px;
    border-radius: 4px;
    height: 60px;
    justify-content: space-between;
    align-items: center;
    padding: 0 12px;
}

.input-wrapper {
    border-radius: 4px;
    background-color: #F4EEF7;
    display: flex;
    flex-direction: column;
    margin: 6px;
    padding: 0 10px;
}
.drop-down {
    color: #52526A;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0;
    line-height: 26px;
    width: 170px;
    height: 40px;
    border-radius: 4px;
}

.option-line {
    height: 34px;
    width: 298px;
    color: #52526A;
    font-family: "HK Grotesk";
    font-size: 16px;
    font-weight: 600;
    letter-spacing: 0;
    line-height: 26px;
}
.input-text {
    box-sizing: border-box;
    height: 32px;
    width: 238px;
    border: 1px solid #EAE8EC;
    border-radius: 4px;
    background-color: #FFFFFF;
    color: #52526A;
    font-family: "HK Grotesk";
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 21px;
    padding: 0 10px;
}

.input-cost {
    box-sizing: border-box;
    height: 32px;
    width: 91px;
    border: 1px solid #EAE8EC;
    border-radius: 4px;
    background-color: #FFFFFF;
    color: #52526A;
    font-family: "HK Grotesk";
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 21px;
    text-align: end;
    padding: 0 10px;
}

.input-asd {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    padding: 8px 0;
    border-bottom: 1px solid #E8DFED;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}
.list-total {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    border-bottom: 1px solid #E8DFED;
}

.list-label {
    color: #250B38;
    font-family: "HK Grotesk";
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 26px;
}

.list-sum {
    color: #250B38;
    font-family: "HK Grotesk";
    font-size: 18px;
    font-weight: bold;
    letter-spacing: 0;
    line-height: 21px;
}
.total-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.total {
    color: #250B38;
    font-family: "HK Grotesk";
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 26px;
    margin-right: 10px;
  }

.total-sum {
    color: #250B38;
    font-family: "HK Grotesk";
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 0;
}
